@tailwind base;
@tailwind components;
@tailwind utilities;

/* Ezra Brand Colors */
:root {
  --navy-blue: #1B365D;
  --vibrant-orange: #FF6B35;
  --rich-purple: #6B46C1;
  --warm-cream: #F5E6B3;
  --light-cream: #FDF9F0;
}

/* Ubuntu Pattern Background */
.ubuntu-pattern {
  background-image: 
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 2px,
      rgba(255, 107, 53, 0.05) 2px,
      rgba(255, 107, 53, 0.05) 4px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 2px,
      rgba(107, 70, 193, 0.05) 2px,
      rgba(107, 70, 193, 0.05) 4px
    );
}

/* Ezra Logo Component */
.ezra-logo {
  display: flex;
  align-items: center;
}

.ezra-logo-icon {
  width: 40px;
  height: 40px;
  margin-right: 12px;
  position: relative;
}

.ezra-logo-pin {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--navy-blue) 0%, var(--vibrant-orange) 50%, var(--rich-purple) 100%);
  border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px rgba(27, 54, 93, 0.3);
}

.ezra-logo-pin::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 2px,
    rgba(255,255,255,0.1) 2px,
    rgba(255,255,255,0.1) 4px
  );
}

.ezra-logo-sun {
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translateX(-50%);
  width: 10px;
  height: 10px;
  background: var(--warm-cream);
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(245, 230, 179, 0.6);
}

.ezra-logo-handshake {
  font-size: 14px;
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translateX(-50%);
  filter: drop-shadow(0 1px 2px rgba(0,0,0,0.3));
  z-index: 2;
}

.ezra-logo-pattern {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 35%;
  background: linear-gradient(0deg, var(--rich-purple) 0%, transparent 100%);
}

.ezra-logo-text {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--navy-blue);
  letter-spacing: -0.02em;
}

.dashboard-card {
  @apply bg-white rounded-lg shadow-sm border border-gray-200 p-6;
  background: linear-gradient(135deg, #ffffff 0%, var(--light-cream) 100%);
  transition: all 0.3s ease;
}

.dashboard-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(27, 54, 93, 0.1);
}

.stat-card {
  @apply bg-white rounded-lg shadow-sm border border-gray-200 p-6 transition-all duration-300;
  background: linear-gradient(135deg, #ffffff 0%, var(--light-cream) 100%);
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 107, 53, 0.1), transparent);
  transition: left 0.5s ease;
}

.stat-card:hover::before {
  left: 100%;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 35px rgba(255, 107, 53, 0.15);
}

.table-row {
  @apply border-b border-gray-200 hover:bg-gray-50 transition-colors;
}

.btn-primary {
  background: var(--vibrant-orange);
  @apply text-white px-4 py-2 rounded-lg font-medium transition-all duration-200;
  box-shadow: 0 4px 12px rgba(255, 107, 53, 0.3);
}

.btn-primary:hover {
  background: #e55a2b;
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(255, 107, 53, 0.4);
}

.btn-secondary {
  background: var(--light-cream);
  color: var(--navy-blue);
  @apply px-4 py-2 rounded-lg font-medium transition-all duration-200;
  border: 1px solid var(--warm-cream);
}

.btn-secondary:hover {
  background: var(--warm-cream);
  transform: translateY(-1px);
}

.btn-danger {
  @apply bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 transition-colors font-medium;
}

.btn-success {
  @apply bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors font-medium;
}

.status-badge {
  @apply px-3 py-1 rounded-full text-xs font-semibold;
}

.status-active {
  @apply bg-green-100 text-green-800;
}

.status-pending {
  @apply bg-yellow-100 text-yellow-800;
}

.status-suspended {
  @apply bg-red-100 text-red-800;
}

.status-completed {
  @apply bg-blue-100 text-blue-800;
}

.sidebar-link {
  @apply flex items-center px-4 py-3 text-gray-700 transition-all duration-200;
  border-radius: 8px;
  margin: 2px 8px;
}

.sidebar-link:hover {
  background: var(--light-cream);
  color: var(--navy-blue);
  transform: translateX(4px);
}

.sidebar-link.active {
  background: linear-gradient(135deg, var(--vibrant-orange), var(--rich-purple));
  color: white;
  box-shadow: 0 4px 12px rgba(255, 107, 53, 0.3);
}

.sidebar-link.active:hover {
  transform: translateX(0);
}